.footer-text {
	font-size: clamp(1.5rem, 5vw, 3rem);
	line-height: 1.5;
	font-weight: 500;
}

.footer-link {
	font-size: clamp(0.75rem, 5vw, 1.5rem);
	line-height: 1.5;
	font-weight: 500;
	color: #363636;
}

.footer-year {
	font-size: clamp(10px, 5vw, 12px);
	line-height: 1.5;
	font-weight: 400;
	color: #363636;
}

.footer-word-circled {
	background-image: url(/src/icons/circle.svg);
	overflow: visible;
	display: inline-block;
	transform: translateX(13px);
}


@media screen and (max-width: 1024px){
	.footer-word-circled {
		background-image: url(/src/icons/circle-sm.svg);
		transform: translateX(7px);
	}
}